@font-face {
  font-family: myFirstFont;
  src: url(./assets/fonts/Alice-Regular.ttf);
}
*{
  box-sizing: border-box;
  /*border: 1px solid red;*/
}
html {
  margin: 0;
}
body {
  margin: 0 auto;
  font-family: -apple-system, myFirstFont,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*font-size: 0.75em;*/
  max-width: 1420px;
  display: flex;
  justify-content: center;
}

code, h1 {
  font-family: myFirstFont, monospace;
}

svg text {
  font-family: myFirstFont, monospace;
  text-anchor: middle;
  fill: url(#linear-gradient);
  stroke: transparent;
  /*filter: drop-shadow(-2px 0 1px #000000cc)*/
}
